.App {
  text-align: center;
  padding: 30px;
  direction: rtl;
}
.emptyBTN{
  background-color: #ffffff;
  border: 0ch;
  cursor: pointer;
  position: absolute;
  
}
.link_text{
  color: cornflowerblue;
  cursor: pointer;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.leftDiv{
  float: right;
}
.App-link {
  color: #61dafb;
}

.aSiteSR {
    text-align: left;
    padding: 5px;
  }
.exContent:link {
    color: #F0F8FF;
  }
  
  /* visited link */
.exContent:visited {
    color: #F0F8FF;
  }
  
  /* mouse over link */
  .exContent:hover {
    color: #F0F8FF;
  }
  
  /* selected link */
  .exContent:active {
    color: #F0F8FF;
  }

.ex_content_partition{
    color: #F0F8FF;
}
.aSiteRef {
    text-align: right;
    margin: 10px;
  }
.title {
    text-align: right;
    padding-top: 7px;
  }
.subject {
    text-align: right;
    padding: 2px;
  }
#findelTheme{
    margin-bottom: 5px;
    align-self: center;
}

#searchBTN{
    margin-right: 2px;
}

#simple_search_radio_button{
    margin-right: 20px;
}

#show_more_content_button{
